<template>
  <div class="accountingprintmanage">
    <v-row>
      <v-col cols="12" md="6">
        <h2 class="subheading grey--text">
          <v-icon class="mx-1">mdi-cash-usd-outline</v-icon>
          <span>會計列印管理</span>
        </h2>
      </v-col>
      <v-col cols="12" md="6" class="text-right">
        <!-- 功能列表 -->
                <v-menu transition="scale-transition" offset-y>
                  <template v-slot:activator="{ on }">
                    <v-btn color="primary" dark v-on="on">功能</v-btn>
                  </template>
                  <v-list>
                    <v-list-item>
                        <v-list-item-title>
                          <v-btn class="mx-1 primary" @click.stop="goAccounting">回會計管理頁面</v-btn>
                        </v-list-item-title>
                      </v-list-item>
                    <v-list-item>
                      <v-list-item-title>
                        <v-btn class="success mx-1" @click.stop="getAllNotPaid">選擇未付款</v-btn>
                      </v-list-item-title>
                    </v-list-item>
                    <v-list-item>
                      <v-list-item-title>
                        <v-btn class="error mx-1" @click.stop="cancelAllNotPaid">取消選擇</v-btn>
                      </v-list-item-title>
                    </v-list-item>
                  </v-list>
                </v-menu>
      </v-col>

    </v-row>

    <v-card>
      <v-card-title>
        <v-spacer></v-spacer>
      </v-card-title>

      <v-card-text>
        <v-row>
          <v-col cols="3">
            <v-card>
              <v-card-title class="primary lighten-2 white--text">病歷資料列表
                <v-spacer></v-spacer>
                
              </v-card-title>
              <v-card-text class="mt-2">
                <v-toolbar dense>
                  <v-text-field
                    hide-details
                    prepend-icon="mdi-magnify"
                    single-line
                    label="搜尋"
                    v-model="serachKey"
                  ></v-text-field>
                </v-toolbar>

                <div style="overflow:auto">
                  <v-list class="mt-2" height="60vh">
                    <v-list-item-group v-model="model" color="primary">
                      <v-list-item
                        v-for="(item, i) in searchfilter"
                        :key="i"
                        @click="getUserData(item)"
                      >
                        <v-list-item-icon class="mx-2">
                          <v-icon>mdi-account</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                          <v-list-item-title>{{item.name}} 床號:{{item.bed_name}}</v-list-item-title>
                          <v-list-item-subtitle>病歷號:{{item.p_code}}</v-list-item-subtitle>
                        </v-list-item-content>
                      </v-list-item>
                    </v-list-item-group>
                  </v-list>
                </div>
              </v-card-text>
            </v-card>
          </v-col>
          <v-col cols="9">
            <v-card>
              <v-card-title class="primary lighten-2 white--text">
                每月費用列表
                <v-spacer></v-spacer>
                {{selectUser.name}} 病歷號:{{selectUser.p_code}} 床號:{{selectUser.bed_name}}
              </v-card-title>
              <v-card-text>
                <v-simple-table fixed-header class="text-no-wrap" height="250px">
                  <template v-slot:default>
                    <thead style="background-color: #e3f2fd;">
                      <tr>
                        <th>選取</th>
                        <th>月份</th>
                        <th>應收帳款</th>
                        <th>已付款金額</th>
                        <th>當月結餘</th>
                        <th>備註</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in paymentfilter" :key="index">
                        <td>
                          <v-checkbox v-model="item.check"></v-checkbox>
                        </td>
                        
                        <td>
                          {{item.month}}
                          <v-chip class="red white--text" small v-if="(item.paid_price - item.total_price) < 0">未付款</v-chip>
                        </td>
                        <td>{{item.total_price}}</td>
                        <td>{{item.paid_price}}</td>
                        <td>{{item.paid_price - item.total_price}}</td>
                        <td>{{item.note}}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </v-card-text>
            </v-card>

            <v-card class="mt-2">
              <v-card-title class="primary lighten-2 white--text">
                已選取資料 - 已選取 {{checkedPaymentfilter.length}} 筆
                <v-spacer></v-spacer>
                <!-- <v-btn class="success mx-1" @click.stop="getAllNotPaid">選擇全部未付款資料</v-btn> -->
                <v-btn
                  class="error"
                  @click="printConfirm"
                  v-if="checkedPaymentfilter.length > 0"
                >確認列印</v-btn>
              </v-card-title>
              <v-card-text>
                <v-simple-table fixed-header class="text-no-wrap">
                  <template v-slot:default>
                    <thead style="background-color: #e3f2fd;">
                      <tr>
                        <th>病歷號</th>
                        <th>姓名</th>
                        <th>床號</th>
                        <th>月份</th>
                        <th>應收帳款</th>
                        <th>已付款金額</th>
                        <th>當月結餘</th>
                        <th>備註</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in checkedPaymentfilter" :key="index">
                        <td>
                          <v-icon color="red" @click="item.check=''">mdi-close</v-icon>
                          {{item.p_code}}
                        </td>
                        <td>{{item.name}}</td>
                        <td>{{item.bed_name}}</td>
                        <td>{{item.month}}</td>
                        <td>{{item.total_price}}</td>
                        <td>{{item.paid_price}}</td>
                        <td>{{item.paid_price - item.total_price}}</td>
                        <td>{{item.note}}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
    <!-- <pre> {{paymentItems}} </pre> -->
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  data() {
    return {
      items: [], //病歷資料
      paymentItems: [], //所有每月應收帳款
      serachKey: "", //病歷搜尋欄
      model: "", //list初選值
      selectUser: [] //目前選擇病歷
      // nowday: dayjs().format("YYYY-MM-DD"), //日期暫存
      // nowmonth: dayjs().format("YYYY-MM"), //日期暫存
    };
  },

  computed: {
    searchfilter() {
      var keys = this.serachKey.split(" ");
      var str = "";

      return keys.reduce(function(prev, element) {
        return prev.filter(item => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
    },
    paymentfilter() {
      //取出指定病歷的每月應收帳款
      return this.paymentItems.filter(item => {
        // return item.u_snkey == this.selectUser.snkey;
        if (item.user_snkey == this.selectUser.snkey) {
          // item["p_code"] = this.selectUser.p_code;
          // item["name"] = this.selectUser.name;
          // item["sex"] = this.selectUser.sex;
          // item["birthday"] = this.selectUser.birthday;
          // item["bed_name"] = this.selectUser.bed_name;
          return item;
        }
      });
    },
    checkedPaymentfilter() {
      //取出己選取的評估單
      var i = this.paymentItems.filter(item => {
        return item.check == true;
      });
      return this.sortBybedname(i);
    }
  },

  mounted() {
    // this.auth = this.$store.state.pData;
    this.getAllData();
    this.getPayment();
  },

  methods: {
    goAccounting() {
      this.$router.push("/main/accountinglist");
    },
    //選擇全部未付款資料
    getAllNotPaid(){
      this.paymentItems.forEach(i=>{
        if (parseInt(i.total_price) > parseInt(i.paid_price)){
          i.check = true;
          // return i
        }
      })
    },
    //取消選擇所有選取待列印的資料
    cancelAllNotPaid(){
      this.paymentItems.forEach(i=>{
        i.check = false;
      })
    },
    //病歷依床號排序
    sortBybedname(items) {
      return items.sort(function(a, b) {
        var a = a.bed_name;
        var b = b.bed_name;

        if (a === b) {
          return 0;
        } else if (a === null) {
          return 1;
        } else if (b === null) {
          return -1;
        } else {
          return a > b ? 1 : -1;
        }
      });
    },
    //依月份排序
    sortBymonth(items) {
      return items.sort(function(a, b) {
        return a.month < b.month ? 1 : -1;
      });
    },
    // console.log("取得所有病歷資料");
    getAllData() {
      //取得所有病歷資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料
      this.axios
        .post("user/getAll/" + this.$store.state.databaseName)
        .then(rs => {
          this.items = rs.data;
          this.items = this.sortBybedname(this.items);
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    getPayment() {
    // console.log("取得全部每月應收帳款資料payment");
      //取得所有營養評估單資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post("payment/getall/" + this.$store.state.databaseName + "/payment")
        .then(rs => {
          // console.log(rs.data);
          var rsData = rs.data.map(item => {
            item["check"] = "";
            return item;
          });
          this.paymentItems = this.sortBymonth(rsData);
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    getUserData(item) {
      //取得點選的病歷資料 -> 顯示指定病歷的每月應收帳款
      this.selectUser = item;
    },
    printConfirm() {
      //判斷確認列印
      this.$confirm("確認顯示列印畫面?").then(res => {
        if (res) {
          // let routeData = this.$router.resolve({ path: '/reportpreview', query: {  id: id } });
          // sessionStorage.setItem("printData", JSON.stringify(this.checkedPaymentfilter));
          // let routeData = this.$router.resolve({
          //   path: "/print/accountingmultiprint"
          // });
          // window.open(routeData.href, "_blank");

          let routeData = this.$router.resolve({path: "/print/accountingmultiprint",query:{data:JSON.stringify(this.checkedPaymentfilter)}});
          window.open(routeData.href, "_blank");
        }
      });
    },
    //判斷是否付清
    // checkPaid(item) {
    //   if ((item.totalprice - item.paidprice) >= 0){
    //     return false
    //   }
    // }
  }
};
</script>


<style lang="scss">
.accountingprintmanage {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1rem;
      font-family: "微軟正黑體";
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>